
body{
  margin:0px !important;
  width: 100%;
  height: 100vh;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  width: 350px;
  height: 600px;
  display: block;
  margin: auto;
  margin-top:50px;
  background: white;
  border-radius: 10px;
  -webkit-box-shadow: 5px 70px 25px 2px #ccc;
          box-shadow: 5px 70px 25px 2px #ccc;
}
#app > div {
  height: 100%;
  width: 100%;
}
.topbox-container{
  width: 100%;
  height:40%;
  background: url(https://parthkamdar.github.io/chat/static/img/top-background.c5e4691.png) no-repeat;
  background-size: cover;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  display: block;
}
.topbox-container h3{
  text-align: center;
  color: white;
  font-size: 2em;
  margin:  0px;
  margin-top: 8%;
  display: inline-block;
}
.topbox-container p{
  text-align: center;
  color: white;
}
.topbox-container img{
  width: 100px;
  margin-top: 10%;
}
.displaybox-container{
  width: 100%;
  background: white;
  border-radius: 10px;
  display: block;
}
.image-row{
  margin-top: 20%;
}
.image-row img{
  width: 10%;
  display: -webkit-inline-box;
  display: -ms-inline-flexbox;
  display: inline-flex;
}
.displaybox-container p{
  margin-top: 10%;
}
.displaybox-container button{
  margin-top:20%;
  width: 60%;
  height: 50px;
  border: none;
  background: #116f8a;
  border-radius: 5px;
  color:white;
}
button:focus{
  outline: none;
}
